    transform: scale(1.2);
    transition: 1s ease;
}

.imgleave .v-image__image {
    transition: 1s ease;
}

.before-enter {
    opacity: 0;
    transform: translateY(100px);
    transition: all 2s ease-out;
}

.enter {
    opacity: 1;
    transform: translateY(0px);
}
